
    import {
        defineComponent
    } from '@vue/runtime-core';

    export default defineComponent({
        components: {},
        emits: [],
        props: {},
        data() {
            return {
                path: ((document.head.querySelector('meta[name="api-base-url"]') as any) !.content as string)
            };
        },
        mounted() {},
        watch: {},
        methods: {
            goToPrevious() {
                window.location.href = `${this.path}/app/inicio`;
            }
        },
    })

